import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

const resourceHost = 'http://127.0.0.1:3000'

export default new Vuex.Store({
  state: {
    studentID: null,
    accessToken: null,
    briefCareer: null,
    career: null,
    currPage: '/'
  },
  mutations: {
    setBriefCareer (state, payload) {
      state.briefCareer = payload
    },
    setCareer (state, payload) {
      state.career = payload
    },
    setStudentID (state, payload) {
      state.studentID = payload
    },
    setToken (state, payload) {
      state.accessToken = payload
    },
    REGISTER (state, { accessToken }) {
      state.accessToken = accessToken
    },
    LOGIN (state, { accessToken }) {
      state.accessToken = accessToken
    },
    LOGOUT (state) {
      state.accessToken = null
    },
    setCurrPage (state, payload) {
      state.currPage = payload
    }
  },
  getters: {
    getStudentID (state) {
      return state.studentID
    },
    getBriefCareer (state) {
      return state.briefCareer
    },
    getCareer (state) {
      return state.career
    },
    getToken (state) {
      return state.token
    },
    getCurrPage (state) {
      return state.currPage
    }
  },
  actions: {
    REGISTER ({ commit }, { studentNum, password }) {
      return axios.post(`${resourceHost}/api/auth/register`, { studentNum, password }).then(({ data }) => {
        commit('REGISTER', data)
        axios.defaults.headers.common['Authorization'] = data.token
      })
    },
    LOGIN ({ commit }, { studentNum, password }) {
      return axios.post(`${resourceHost}/api/auth/login`, { studentNum, password }).then(({ data }) => {
        commit('LOGIN', data)
        axios.defaults.headers.common['Authorization'] = data.token
      })
    },
    LOGOUT ({ commit }) {
      commit('LOGOUT')
    }
  },
  modules: {
  }
})
