<template>
  <div class="career">
    <v-container>
      <v-flex v-bind:key="item">
        <v-textarea
          outlined
          name="input-7-4"
          label="Outlined textarea"
          v-bind:readonly="true"
          v-model="item"
        ></v-textarea>
      </v-flex>
    </v-container>
    <v-btn v-if="isAuth" @click="edit">수정</v-btn>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data: () => ({
    item: undefined,
    isAuth: false
  }),
  computed: {
    studentID: function() {
      return this.$session.get("studentID");
    },
    accessToken: function() {
      return this.$session.get("accessToken");
    }
  },
  created: async function() {
    if (this.accessToken) {
      this.isAuth = true;
    }
    await axios
      .get(`/api/career/${this.studentID}`, {
        studentID: this.studentID
      })
      .then(body => {
        this.item = body.data.careers;
        var tempCareers = this.item;
        this.$store.commit("setCareer", tempCareers);
        this.$session.set("career", tempCareers);
      })
      .catch(err => {
        alert(err + "올바르지 않은 학번입니다.");
        this.$router.push({ name: `home` });
      });
    if (this.accessToken) {
      this.isAuth = true;
    }
  },
  methods: {
    edit() {
      this.$router.push({ name: `careerEdit` });
    }
  }
};
</script>
