<template>
  <div class="portfolio">
    <v-container fluid grid-list-md>
      <v-layout row wrap>
        <v-flex xs12 sm6 md4 lg3 xl4 v-bind:key="item" v-for="item in items">
          <v-card>
            <v-img :src="item.photo" width="1000px" height="200px"></v-img>
            <v-card-title primary-title>
              <div>
                <h3 class="headline mb-0">{{item.title}}</h3>
              </div>
            </v-card-title>
            <v-card-actions>
              <v-btn flat color="orange">좋아요</v-btn>
              <v-btn flat color="orange">싫어요</v-btn>
              <v-btn icon @click="item.show = !item.show">
                <v-icon>{{ item.show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
              </v-btn>
            </v-card-actions>
            <v-expand-transition>
              <div v-show="item.show">
                <v-divider></v-divider>
                <div v-if="isAuth">
                  <v-form @submit.prevent="editDescription(item)" id="check-modify-form">
                    <v-textarea
                      outlined
                      name="input-7-4"
                      label="Outlined textarea"
                      v-model="item.description"
                    ></v-textarea>
                    <v-btn type="submit" form="check-modify-form">수정</v-btn>
                    <v-btn type="button" form="check-modify-form" @click="deletPortfolio(item)">삭제</v-btn>
                  </v-form>
                </div>
                <div v-else>
                  <v-card-text>{{item.description}}</v-card-text>
                </div>
              </div>
            </v-expand-transition>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
    <!-- <v-btn v-if="isAuth" @click="edit">확인</v-btn> -->
    <v-btn v-if="isAuth" @click="edit">수정</v-btn>
  </div>
</template>
<script>
import axios from "axios";

export default {
  data: () => ({
    isAuth: false,
    items: []
  }),
  computed: {
    studentID: function() {
      return this.$session.get("studentID");
    },
    accessToken: function() {
      return this.$session.get("accessToken");
    }
  },
  created: async function() {
    if (this.accessToken) {
      this.isAuth = true;
    }
    console.log(this.$session.get("accessToken"));
    console.log(this.studentID);
    await axios
      .get(`/api/portfolio/${this.studentID}`, {})
      .then(body => {
        var portfolios = body.data.portfolios;
        var tempItem = {};
        console.log(portfolios.length);
        for (var i = 0; i < portfolios.length; i++) {
          tempItem._id = portfolios[i]._id;
          tempItem.title = portfolios[i].title;
          tempItem.description = portfolios[i].description;
          tempItem.photo = "/images/" + portfolios[i].photo;
          tempItem.show = false;
          console.log(tempItem.photo);
          this.items.push(tempItem);
          tempItem = {};
        }
      })
      .catch(err => {
        alert(err + "올바르지 않은 학번입니다.");
        this.$router.push({ name: `home` });
      });
    if (this.accessToken) {
      this.isAuth = true;
    }
  },
  methods: {
    edit() {
      this.$router.push({ name: `portfolioEdit` });
    },
    editDescription: async function(item) {
      var portfolioID = item._id;
      var title = item.title;
      var description = item.description;
      console.log(localStorage.getItem("accessToken"));
      await axios
        .put(
          `/api/portfolio/edit`,
          { portfolioID, title, description },
          {
            headers: { Authorization: localStorage.getItem("accessToken") }
          }
        )
        .then(() => {
          this.$router.push({ name: `portfolio` });
        })
        .catch(err => alert(err));
    },
    deletPortfolio: async function(item) {
      var portfolioID = item._id;
      await axios
        .delete(
          `/api/portfolio/delete`,
          { data: { portfolioID } },
          {
            headers: { Authorization: localStorage.getItem("accessToken") }
          }
        )
        .then(() => {
          this.$router.push({ name: `portfolio` });
        })
        .catch(err => alert(err));
    }
  }
};
</script>
