<template>
  <div class="career">
    <p style="font-size: 100px">CareerEdit</p>
    <v-container>
      <v-flex>
        <v-form @submit.prevent="edit" id="check-modify-form">
          <v-textarea outlined name="input-7-4" label="Outlined textarea" v-model="history"></v-textarea>
          <v-btn type="submit" form="check-modify-form">수정</v-btn>
          <v-btn @click="before">이전으로</v-btn>
        </v-form>
      </v-flex>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data: () => ({
    isAuth: true,
    history: ""
  }),
  computed: {
    studentID: function() {
      return this.$session.get("studentID");
    },
    accessToken: function() {
      return this.$session.get("accessToken");
    }
  },
  created() {
    this.history = this.$session.get("career");
  },
  methods: {
    edit: async function() {
      var career = this.history;
      this.$store.commit("setCareer", career);
      this.$session.set("career", career);
      console.log(career);
      await axios
        .post(
          `/api/career/push`,
          { career },
          {
            headers: { Authorization: localStorage.getItem("accessToken") }
          }
        )
        .then(() => {
          this.$router.push({ name: `career` });
        })
        .catch(err => alert(err));
    },
    before() {
      this.$router.push({ name: `career` });
    }
  }
};
</script>
