<template>
  <v-app id="sandbox">
    <v-navigation-drawer
      v-model="primaryDrawer.model"
      :clipped="primaryDrawer.clipped"
      :floating="primaryDrawer.floating"
      :mini-variant="primaryDrawer.mini"
      :permanent="primaryDrawer.type === 'permanent'"
      :temporary="primaryDrawer.type === 'temporary'"
      app
      overflow
    >
      <v-list dense rounded>
        <v-list-item v-for="item in items" :key="item.title" link @click="itemRoute(item)">
          <v-list-item-icon>
            <v-icon>{{item.icon}}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{item.title}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-app-bar :clipped-left="primaryDrawer.clipped" app>
      <v-app-bar-nav-icon
        v-if="primaryDrawer.type !== 'permanent'"
        @click.stop="primaryDrawer.model = !primaryDrawer.model"
      />
      <v-toolbar-title>포트폴리오</v-toolbar-title>
    </v-app-bar>
    <v-content>
      <v-container fluid>
        <v-row align="center" justify="center">
          <router-view></router-view>
        </v-row>
      </v-container>
    </v-content>
    <v-footer :inset="footer.inset" app>
      <span @click="login" class="px-4">&copy; {{ new Date().getFullYear() }}</span>
      <span @click="register" class="px-4">Copyright KAU_Web</span>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  data: () => ({
    items: [
      {
        title: '포트폴리오',
        icon: 'mdi-account-card-details',
        route: 'portfolio'
      },
      { title: '약력', icon: 'mdi-account-outline', route: 'brief' },
      { title: '경력', icon: 'mdi-format-float-left', route: 'career' }
    ],
    drawers: ['Default (no property)', 'Permanent', 'Temporary'],
    primaryDrawer: {
      model: null,
      type: 'default (no property)',
      clipped: false,
      floating: false,
      mini: false
    },
    footer: {
      inset: false
    }
  }),
  computed: {
    studentID: function () {
      return this.$session.get('studentID')
    }
  },
  methods: {
    register () {
      this.$router.push('/register')
    },
    login () {
      this.$router.push('/login')
    },
    itemRoute (item) {
      if (item.route === 'portfolio') {
        this.$router.push({ name: `${item.route}` })
      } else if (item.route === 'brief') {
        this.$router.push({ name: `${item.route}` })
      } else if (item.route === 'career') {
        this.$router.push({ name: `${item.route}` })
      }
    }
  }
}
</script>
