<template>
  <div class="brief">
    <h1>BriefEdit</h1>
    <v-container>
      <v-flex>
        <v-form @submit.prevent="edit" id="check-modify-form">
          <v-textarea outlined name="input-7-4" label="Outlined textarea" v-model="briefHistory"></v-textarea>
          <v-btn v-if="isAuth" type="submit" form="check-modify-form">수정</v-btn>
          <v-btn @click="before">이전으로</v-btn>
        </v-form>
      </v-flex>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data: () => ({
    isAuth: true,
    briefHistory: ""
  }),
  computed: {
    studentID: function() {
      return this.$session.get("studentID");
    },
    accessToken: function() {
      return this.$session.get("accessToken");
    }
  },
  created() {
    this.briefHistory = this.$session.get("briefCareer");
  },
  methods: {
    edit: async function() {
      var briefcareer = this.briefHistory;
      this.$store.commit("setBriefCareer", briefcareer);
      this.$session.set("briefCareer", briefcareer);
      console.log(briefcareer);
      await axios
        .post(
          `/api/briefcareer/push`,
          { briefcareer },
          {
            headers: { Authorization: localStorage.getItem("accessToken") }
          }
        )
        .then(() => {
          this.$router.push({ name: `brief` });
        })
        .catch(err => alert(err));
    },
    before() {
      this.$router.push({ name: `brief` });
    }
  }
};
</script>
