<template>
  <v-row justify="center">
    <p>Student {{$route.params.studentID}}</p>
    <v-dialog v-model="dialog" max-width="600px">
      <v-card>
        <v-card-title>
          <span></span>
        </v-card-title>
        <v-col cols="12">
          <v-text-field v-model="studentID" label="Student-Id" placeholder="학번을 입력하세요" outlined></v-text-field>
          <v-btn color="gray darken-1" text @click="onComplete">Complete</v-btn>
          <v-btn color="gray darken-1" text @click="onCancel">Cancel</v-btn>
        </v-col>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
export default {
  name: "home",
  data: () => ({
    studentID: "",
    dialog: false
  }),
  mountsed() {
    var accessToken = this.$session.get("accessToken");
    console.log(accessToken);
    if (accessToken) {
      this.dialog = false;
    } else if (this.studentID) {
      this.dialog = false;
      console.log(this.studentID);
    } else {
      this.dialog = true;
    }
  },
  computed: {},
  created() {},
  methods: {
    onComplete() {
      this.dialog = false;
      this.$store.commit("setStudentID", this.studentID);
      this.$session.set("studentID", this.studentID);
      this.$router.push({ name: `portfolio` });
    },
    onCancel() {
      this.dialog = false;
    }
  }
};
</script>
