import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Portfolio from '../views/Portfolio.vue'
import PortfolioEdit from '../views/PortfolioEdit.vue'
import Brief from '../views/Brief.vue'
import BriefEdit from '../views/BriefEdit.vue'
import Career from '../views/Career.vue'
import CareerEdit from '../views/CareerEdit.vue'
import Login from '../views/Login.vue'
import Register from '../views/Register.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/register',
    name: 'register',
    component: Register
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/portfolio',
    name: 'portfolio',
    component: Portfolio
  },
  {
    path: '/portfolio/push',
    name: 'portfolioEdit',
    component: PortfolioEdit
  },
  {
    path: '/briefcareer',
    name: 'brief',
    component: Brief
  },
  {
    path: '/briefcareer/push',
    name: 'briefEdit',
    component: BriefEdit
  },
  {
    path: '/career',
    name: 'career',
    component: Career
  },
  {
    path: '/career/push',
    name: 'careerEdit',
    component: CareerEdit
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
