<template>
  <div class="portfolio">
    <v-container>
      <v-flex>
        <v-form @submit.prevent="onSubmit" id="check-submit-form">
          <v-text-field v-model="history" label="Title" required></v-text-field>
          <v-textarea outlined name="input-7-4" label="Description" v-model="description"></v-textarea>
          <input type="file" class="ma-2" @change="onFileSelected" />
          <br />
          <v-btn type="submit" class="ma-2" outlined color="indigo" form="check-submit-form">저장</v-btn>
          <v-btn class="ma-2" outlined color="indigo" @click="before">이전으로</v-btn>
        </v-form>
      </v-flex>
    </v-container>
  </div>
</template>
<script>
import axios from "axios";

export default {
  components: {},
  data: () => ({
    history: undefined,
    description: undefined,
    photo: undefined
  }),
  computed: {},
  created() {},
  methods: {
    onFileSelected(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) {
        return;
      }
      this.photo = files[0];
    },
    before() {
      this.$router.push({ name: `portfolio` });
    },
    onSubmit: async function() {
      if (this.history === undefined) {
        alert("타이틀을 작성하세요!");
        return;
      }
      if (this.description === undefined) {
        alert("포트폴리오 설명을 작성하세요!");
        return;
      }
      if (this.photo === undefined) {
        alert("포트폴리오 사진을 선택하세요!");
        return;
      }
      let formData = new FormData();
      formData.append("title", this.history);
      formData.append("description", this.description);
      formData.append("photo", this.photo);
      await axios
        .post(`/api/portfolio/push`, formData, {
          headers: {
            Content: "multipart/form-data",
            Authorization: localStorage.getItem("accessToken")
          }
        })
        .then(() => {
          this.$router.push({ name: `portfolio` });
        })
        .catch(err => alert(err));
    }
  }
};
</script>
