<template>
  <div class="login">
    <v-form @submit.prevent="submit" id="check-login-form" ref="form" lazy-validation>
      <h1>Sinn In</h1>
      <v-text-field v-model="studentID" :rules="studentIdRules" label="Student ID" required></v-text-field>
      <v-text-field
        v-model="password"
        :type="'password'"
        :rules="passwordRules"
        label="PassWord"
        required
      ></v-text-field>
      <v-btn :disable="!valid" type="submit" form="check-login-form">submit</v-btn>
    </v-form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data: () => ({
    studentID: "",
    valid: true,
    username: "",
    studentIdRules: [
      v => !!v || "Student ID is Required",
      v => /[0-9]{4,}/.test(v) || "숫자만 입력 가능합니다."
    ],
    password: "",
    passwordRules: [
      v => !!v || "Password is Required",
      v =>
        /^[0-9a-zA-Z@#$]{5,}/.test(v) ||
        "5자리 이상의 @ # $ 혹은 숫자, 영어만 입력 가능합니다."
    ]
  }),
  methods: {
    submit: async function() {
      var studentNum = this.studentID;
      var password = this.password;
      if (this.$refs.form.validate()) {
        await axios
          .post(`/api/auth/login`, { studentNum, password })
          .then(body => {
            this.$store.commit("setStudentID", studentNum);
            this.$session.set("studentID", studentNum);
            window.localStorage.setItem("accessToken", body.data.token);
            this.$store.commit("setToken", body.data.token);
            this.$session.set("accessToken", body.data.token);
            this.$router.push({ name: `portfolio` });
          })
          .catch(err => alert(err));
      }
    }
  }
};
</script>
